"use client"

import { useState, Suspense } from "react"
import { signIn } from "next-auth/react"
import { useRouter, useSearchParams } from "next/navigation"
import { Truck, Phone, Lock, Eye, EyeOff, Loader2 } from "lucide-react"
import { AuthLayout } from "@/components/layout"
import { Button, Input, Label } from "@/components/ui"
import toast from "react-hot-toast"

function DeliveryLoginForm() {
  const [phone, setPhone] = useState("")
  const [password, setPassword] = useState("")
  const [showPassword, setShowPassword] = useState(false)
  const [loading, setLoading] = useState(false)
  const router = useRouter()
  const searchParams = useSearchParams()
  const error = searchParams.get("error")

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault()

    if (!phone || !password) {
      toast.error("Please enter phone number and password")
      return
    }

    const cleanPhone = phone.replace(/\D/g, "").slice(-10)
    if (cleanPhone.length !== 10) {
      toast.error("Please enter a valid 10-digit phone number")
      return
    }

    setLoading(true)

    try {
      const result = await signIn("delivery-credentials", {
        phone: cleanPhone,
        password,
        redirect: false,
      })

      if (result?.error) {
        toast.error(result.error)
      } else {
        router.push("/delivery")
        router.refresh()
      }
    } catch (err) {
      toast.error("An error occurred. Please try again.")
    } finally {
      setLoading(false)
    }
  }

  return (
    <>
      {/* Error Message */}
      {error && (
        <div className="mb-5 p-4 bg-red-50 border border-red-200 rounded-lg text-red-600 text-sm">
          Invalid credentials. Please try again.
        </div>
      )}

      {/* Login Form */}
      <form onSubmit={handleSubmit} className="space-y-4">
        <div className="space-y-2">
          <Label htmlFor="phone">Phone Number</Label>
          <div className="relative">
            <Phone className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-[hsl(var(--muted-foreground))]" />
            <Input
              id="phone"
              type="tel"
              value={phone}
              onChange={(e) => setPhone(e.target.value)}
              placeholder="Enter your phone number"
              className="pl-10"
              autoComplete="tel"
              required
            />
          </div>
          <p className="text-xs text-[hsl(var(--muted-foreground))]">Use the phone number provided by admin</p>
        </div>

        <div className="space-y-2">
          <Label htmlFor="password">Password</Label>
          <div className="relative">
            <Lock className="absolute left-3 top-1/2 -translate-y-1/2 w-4 h-4 text-[hsl(var(--muted-foreground))]" />
            <Input
              id="password"
              type={showPassword ? "text" : "password"}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              placeholder="Enter your password"
              className="pl-10 pr-10"
              autoComplete="current-password"
              required
            />
            <button
              type="button"
              onClick={() => setShowPassword(!showPassword)}
              className="absolute right-3 top-1/2 -translate-y-1/2 text-[hsl(var(--muted-foreground))] hover:text-[hsl(var(--foreground))]"
            >
              {showPassword ? <EyeOff className="w-4 h-4" /> : <Eye className="w-4 h-4" />}
            </button>
          </div>
        </div>

        <Button type="submit" loading={loading} className="w-full">
          Sign In
        </Button>
      </form>

      {/* Help Note */}
      <div className="mt-5 p-4 bg-[hsl(var(--muted))] rounded-lg">
        <p className="text-xs text-[hsl(var(--muted-foreground))]">
          <span className="font-semibold text-[hsl(var(--foreground))]">Need help?</span> Contact your administrator if you've forgotten your
          credentials or need to update your phone number.
        </p>
      </div>
    </>
  )
}

export default function DeliveryLoginPage() {
  return (
    <AuthLayout
      title="Delivery Portal"
      subtitle="Sign in to view your deliveries"
      icon={<Truck className="w-7 h-7 text-white" />}
      variant="delivery"
    >
      <Suspense fallback={<div className="flex justify-center py-10"><Loader2 className="w-8 h-8 animate-spin text-primary" /></div>}>
        <DeliveryLoginForm />
      </Suspense>
    </AuthLayout>
  )
}
